<template>
	<div class="login-box">
		<van-nav-bar
			title=""
			left-arrow
			@click-left="onClickLeft($router.back())"
		/>
		<!--  -->
		<van-form
			@submit="onLogin"
			:show-error="false"
			:show-error-message="false"
			@failed="onFailed"
			validate-first
			ref="login-form"
		>
			<van-field
				v-model="user.mobile"
				icon-prefix="iconfont icon"
				left-icon="shouji"
				right-icon="warning-o"
				placeholder=""
				:rules="formRules.mobile"
				name="mobile"
				center
			/>
			<van-field
				v-model="user.code"
				clearabled
				icon-prefix="iconfont icon"
				left-icon="yanzhengma"
				placeholder=""
				:rules="formRules.code"
				name="code"
				center
			>
				<template #button>
					<van-count-down
						v-if="isCountDown"
						:time="1000 * 60"
						format="ss"
						@finish="isCountDown = false"
					/>
					<van-button
						v-else
						native-type="button"
						size="small"
						round
						class="send-msg"
						@click="onSendSms"
						:disabled="isSendSmsLoading"
						>
					</van-button>
				</template>
			</van-field>
			<!--  -->
			<div class="login-container">
				<van-button class="but-login" type="info" block
					>
				</van-button>
			</div>
		</van-form>
		<span>:13911111111</span>
		<span>:246810</span>
	</div>
</template>

<script>
import { getLogin, getSendSms } from "@/api";
import { mapMutations } from "vuex";

export default {
	name: "Login",
	data() {
		return {
			user: {
				mobile: "13911111111",
				code  : "246810"
			},
			formRules: {
				mobile: [
					{ required: true, message: "" },
					{
						pattern: /^1[3456789]\d{9}$/,
						message: ""
					}
				],
				code: [
					{ required: true, message: "" },
					{ pattern: /^\d{6}$/, message: "" }
				]
			},
			//
			isCountDown     : false,
			//
			isSendSmsLoading: false
		};
	},
	methods: {
		...mapMutations({ setUser: "token/setUser" }),
		async onLogin() {
			this.$toast.loading({
				message    : "...",
				forbidClick: true,
				duration   : 0
			});
			try {
				const { data: res } = await getLogin(this.user);
				this.$toast.success("");
				console.log(res);
				//vuextoken
				this.setUser(res);
				//
				await this.$router.push("/my");
			}
			catch (error) {
				console.log(error);
				this.$toast.fail(error.message);
			}
		},
		onFailed(error) {
			if (error.errors[0]) {
				this.$toast({
					message : error.errors[0].message,
					position: "top"
				});
			}
		},
		async onSendSms() {
			try {
				//
				await this.$refs["login-form"].validate("mobile");
				//
				this.isSendSmsLoading = true;
				const res = await getSendSms(this.user.mobile);
				//
				this.isCountDown = true;
			}
			catch (error) {
				//
				let message = "";
				error && error.response && error.response.status === 429
					? //
					  (message = ",")
					: error.name === "mobile"
						? //
					  (message = error.message)
						: (message = "");

				this.$toast({
					message,
					position: "top"
				});
			}
			this.isSendSmsLoading = false;
		}
	}
};
</script>

