	.but-login {
		width: 90%;
		margin: 14px auto;
		background-color: #6db4fb;
		border: none;
	}
}

.send-msg {
	background-color: #ededed;
	width: 80px;
	height: 23px;

	.van-button__text {
		font-size: 11px;
		color: #666666;
	}
}
::v-deep .iconfont {
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
}
::v-deep.login-box {
	& > span {
		display: block;
		text-align: center;
		font-size: 14px;
		color: #666666;
	}
	.van-count-down {
		font-size: 12px;
	}
  .van-nav-bar .van-icon{
    color: #fff;
  }
}
</style>
